html,
body {
  background: black;
  color: #fdfa4a;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  font-family: "Press Start 2P", cursive;
  text-align: center;
}

body {
  font-weight: 400;
}

button {
  font-family: "Press Start 2P", cursive;
  display: block;
  margin: 0 auto;
  border: none;
  background: #49f5ec;
  padding: 10px 20px;
  text-transform: uppercase;
}

h1 {
  font-size: 20px;
  text-align: center;
}

textarea {
  font-family: "Press Start 2P", cursive;
  border: 3px solid black;
  width: 90%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background: #49f5ec;
  padding: 10px;
}

textarea:disabled {
  background-color: #ebebeb;
  cursor: not-allowed;
}

button:disabled {
  background-color: #ebebeb;
  cursor: not-allowed;
}
